<template>
  <div class="modal" tabindex="-1" id="updateModal">
    <div class="modal-dialog modal-dialog-centered modal-xl">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title"><strong> </strong></h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <div class="mb-3">
            <input
              v-model="$store.state.boardad.title"
              type="text"
              class="form-control"
              placeholder=""
            />
          </div>
          <div class="mb-3">
            <div id="divEditorUpdate"></div>
          </div>
          <!--      -->
          <!--   data-fileList  -->
          <div v-if="$store.state.boardad.fileList.length > 0" class="mb-3">
             :
            <span
              ><div
                v-for="(file, index) in $store.state.boardad.fileList"
                class="fileName"
                :key="index"
              >
                {{ file.fileName }}
              </div></span
            >
          </div>
          <div class="form-check form-switch mb-3">
            <input
              v-model="attachFile"
              class="form-check-input"
              type="checkbox"
              value=""
              id="chkFileUploadUpdate"
            />
            <label class="form-check-label" for="chkFileUploadUpdate"
              > </label
            >
          </div>
          <div class="mb-3" v-show="attachFile" id="imgFileUploadUpdateWrapper">
            <input
              @change="changeFile"
              type="file"
              id="inputFileUploadUpdate"
              multiple
            />
            <div id="imgFileUploadUpdateThumbnail" class="thumbnail-wrapper">
              <!-- vue way img   append  , v-for   -->
              <img
                v-for="(file, index) in fileList"
                v-bind:src="file"
                v-bind:key="index"
              />
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            @click="boardUpdate"
            class="btn btn-sm btn-outline"
            data-dismiss="modal"
            type="button"
            style="background-color: #656057; color: white"
          >
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import CKEditor from "@ckeditor/ckeditor5-vue2";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import VueAlertify from "vue-alertify";

Vue.use(CKEditor).use(VueAlertify);

import http from "@/common/axios.js";

export default {
  name: "UpdateModal",
  props: ["board"],
  // data  CKEditor, attachFile, fileList UI  3
  data() {
    return {
      // boardId: 0,
      // title: '',
      CKEditor: "",
      attachFile: false,
      fileList: [], // store  fileList  .    .
    };
  },
  methods: {
    changeFile(fileEvent) {
      if (fileEvent.target.files && fileEvent.target.files.length > 0) {
        for (var i = 0; i < fileEvent.target.files.length; i++) {
          const file = fileEvent.target.files[i];
          this.fileList.push(URL.createObjectURL(file));
        }
      }
    },
    //  actions    . backend async ,   store    .
    boardUpdate() {
      // post form data
      var formData = new FormData();
      formData.append("noticeId", this.$store.state.boardad.boardId); // update  
      formData.append("title", this.$store.state.boardad.title);
      formData.append("content", this.CKEditor.getData()); // store X !!!!

      // file upload
      var attachFiles = document.querySelector("#inputFileUploadUpdate");
      console.log("UpdateModalVue: data 1 : ");
      console.log(attachFiles);

      var cnt = attachFiles.files.length;
      for (var i = 0; i < cnt; i++) {
        formData.append("file", attachFiles.files[i]);
      }
      // not put, REST but FileUpload
      http
        .post(
          "/notices/" + this.$store.state.boardad.boardId, // store boardId
          formData,
          { headers: { "Content-Type": "multipart/form-data" } }
        )
        .then(({ data }) => {
          console.log("UpdateModalVue: data : ");
          console.log(data);
          if (data.result == "login") {
            this.$router.push("/login");
          } else {
            this.$alertify.success(" .");
            this.closeModal();
          }
        })
        .catch((error) => {
          console.log("UpdateModalVue: error ");
          console.log(error);
        });
    },
    closeModal() {
      this.$emit("call-parent-update"); // no parameter
    },
  },
  // modal.show()   mounted() 
  mounted() {
    ClassicEditor.create(document.querySelector("#divEditorUpdate"))
      .then((editor) => {
        this.CKEditor = editor;
      })
      .catch((err) => {
        console.error(err.stack);
      });

    // bootstrap modal show event hook
    // UpdateModal    
    let $this = this;
    this.$el.addEventListener("show.bs.modal", function () {
      $this.CKEditor.setData($this.$store.state.boardad.content);
      //    
      //          .
      this.attachFile = false;
      this.fileList = [];
      document.querySelector("#inputFileUploadUpdate").value = "";
    });
  },
  // watch  X
  // props  X
  // watch: {
  //   board : function(){
  //     // props --> data
  //     this.boardId = this.board.boardId;
  //     this.title = this.board.title;
  //     this.CKEditor.setData( this.board.content );
  //     //    
  //     //    props - board  
  //     // fileList    
  //     //this.fileList = this.board.fileList;

  //     //    
  //     //          .
  //     this.attachFile = false;
  //     this.fileList = [];
  //   }
  // }
};
</script>

<style scoped>
.modal >>> .ck-editor__editable {
  min-height: 300px !important;
}

.modal >>> .thumbnail-wrapper {
  margin-top: 5px;
}

.modal >>> .thumbnail-wrapper img {
  width: 100px !important;
  margin-right: 5px;
  max-width: 100%;
}
</style>
